import React from 'react';
import {Link} from 'react-router';
const Navigation = () => {
    return (
        <div className="nav">
            <div className="nav__link"><Link to='/'></Link></div>
            <div className="nav__link"><Link to='/children'> </Link></div>
            <div className="nav__link"><Link to='/child_tests'></Link></div>
            <div className="nav__link"><Link to='/med_examinations'></Link></div>
            <div className="nav__link"><Link to='/products'></Link></div>
        </div>
    );
};
export default Navigation;


