import DataScheme from '../../Common/Helpers/DataScheme';
class MedExaminationScheme extends DataScheme {
    createDefaultScheme () {
        return {

            child_id : {
                name : 'child_id',
                value : 0,
                label : ' ',
                element_type : 'CustomSelectList',
                width: 200,
                validators: [
                ]
            },
            examination_date : {
                name : 'examination_date',
                value : new Date(),
                label : ' ',
                element_type : 'DatePicker',
                width: 100,
                validators: [
                    {
                        name : 'required'
                    }
                ]
            },
            weight : {
                name : 'weight',
                value : '',
                label : ' ()',
                element_type : 'NumberField',
                width: 100,
                format_params : {
                    units : ''
                },
                field_params : {
                    step : 0.1
                },
                validators: [
                    {
                        name : 'required'
                    }
                ]
            },
            height : {
                name : 'height',
                value : '',
                label : ' ()',
                element_type : 'NumberField',
                width: 100,
                format_params : {
                    units : ''
                },
                field_params : {
                    step : 0.1
                },
                validators: [
                    {
                        name : 'required'
                    }
                ]
            },
            fa_tolerance : {
                name : 'fa_tolerance',
                value : 50,
                label : ' (/)',
                element_type : 'NumberField',
                width: 200,
                format_params : {
                    units : '/'
                },
                field_params : {
                    step : 1
                },
                validators: [
                    {
                        name : 'required'
                    }
                ]
            },
            id : {
                name : 'id',
                value : '',
                element_type : 'HiddenField'
            }
        };
    }
}

export default MedExaminationScheme;



